<template>
<div>
 <div class="header">
    <ul class="header-button-left">
      <li>Cancel</li>
    </ul>
    <ul class="header-button-right">
      <li v-if="activeTabNum == 1" @click="activeTabNum = 2">Next</li>
      <li v-if="activeTabNum == 2" @click="uploadPost">Done</li>
    </ul>
    <img src="./assets/logo.png" class="logo">
  </div>

  <h4>vuexÏóê Ï†ÄÏû•Îêú ÎÇòÏù¥ {{$store.state.age}}</h4>
  <h4>vuexÏóê Ï†ÄÏû•Îêú Ïù¥Î¶Ñ {{$store.getters.getName}}</h4>
  <button @click="$store.state.age++">ÎÇòÏù¥ÎçîÌïòÍ∏∞</button>
  <button @click="$store.commit('addAge')">ÎÇòÏù¥ÎçîÌïòÍ∏∞</button>
  <button @click="$store.commit('changeName','Tom')">Ïù¥Î¶ÑÎ∞îÍæ∏Í∏∞</button>
  <!-- <button @click="$store.dispatch('changeName','Tom')">Ïù¥Î¶ÑÎ∞îÍæ∏Í∏∞</button> -->

  <Body :selectFilter="selectFilter" :filters="filters" :posts="posts" :activeTabNum="activeTabNum" :uploadImage="uploadImage" v-on:writeText="writeText=$event"/>
  
  <button @click="showMore">ÎçîÎ≥¥Í∏∞</button>

  <div class="footer">
    <ul class="footer-button-plus">
      <li>
        <input type="file" id="file" class="inputfile" v-on:change="upload">
        <label for="file" class="input-plus">+</label>
      </li>
    </ul>
  </div>
</div>
</template>

<script>
import axios from 'axios';
import Body from './components/Body.vue';
import posts from './assets/posts.js';
import EventBus from './EventBus.js';

export default {
  name: 'app',
  data(){
    return {
      posts: posts,
      activeTabNum: 0,
      uploadImage: '',
      writeText: '',
      filters: [ "normal", "clarendon", "gingham", "moon", "lark", "reyes", 
      "juno", "slumber", "aden", "perpetua", "mayfair", "rise", "hudson", 
      "valencia", "xpro2", "willow", "lofi", "inkwell", "nashville"],
      selectFilter: ''
    }
  },
  mounted(){
    EventBus.$on('select-filter', (filter) => {
      /* eslint-disable */
      console.log(filter);
      this.selectFilter = filter;
    });
  },
  components: {
    Body
  },
  methods: {
    showMore() {
      axios.get('https://yogoho210.github.io/postdata2.json').then(result => {
        /* eslint-disable */
        console.log(result.data);
        this.posts.push(result.data);
      });
    },
    upload(e) {
      // 1. Îã§ÏùåÌéòÏù¥ÏßÄÎ°ú Ïù¥Îèô
      this.activeTabNum = 1;
      // 2. ÏóÖÎ°úÎìúÌïú Ïù¥ÎØ∏ÏßÄÎ•º Body.vueÏóê vueÏóê Î≥¥Ïó¨Ï§òÏïºÌï®
      let files = e.target.files;
      let reader = new FileReader();
      //ÌååÏùº Í∏¥Î¨∏ÏûêÎ°ú ÏïïÏ∂ï
      reader.readAsDataURL(files[0]);
      //3. bodyÏóê Í∑∏ Ïù¥ÎØ∏ÏßÄÎ•º Î≥¥Ïó¨Ï§òÏïºÌï®(Îç∞Ïù¥ÌÑ∞Î∞îÏù∏Îî©)
      reader.onload = e => {
        /* eslint-disable */
        console.log(e.target.result);
        this.uploadImage = e.target.result;
      }
    },
    uploadPost() {
      // 1. Î©îÏù∏ÌéòÏù¥ÏßÄÎ°ú Ïù¥Îèô
      this.activeTabNum = 0;
      // 2. ÎÇ¥ Í≤åÏãúÎ¨ºÏù¥ Ï†ÄÍ∏∞ Î©îÏù¥ÌéòÏù¥ÏßÄÏóê Î≥¥Ïó¨ÏïºÌï®(this.Ìè¨Ïä§ÌåÖÎì§ÏóêÎã§Í∞Ä Îç∞Ïù¥ÌÑ∞Ïóê Ïö∞Î¶¨ Îç∞Ïù¥ÌÑ∞Î•º Ï∂îÍ∞ÄÌï®)
      var postData = {
        name: "nini",
        userImage: "https://placeimg.com/100/100/arch", 
        postImage: this.uploadImage, 
        likes: 36,
        date: '1208',
        liked: false,
        caption: this.writeText, 
        filter: this.selectFilter
      }
      this.posts.unshift(postData);
    }
  }
}
</script>

